<template>
  <el-form class="form" :inline="true" @submit.native.prevent="onSubmit" :model="form" label-width="100px">
    <el-row :gutter="24" v-loading="loading">
      <el-row v-for="(item, key) in form.prize" :key="key">
        <el-col>
          <el-form-item :label="'名称' + (key + 1)">
            <el-input v-model="item.name" placeholder="奖品名"></el-input>
          </el-form-item>
          <el-form-item :label="'概率' + (key + 1)">
            <el-input v-model="item.pro" placeholder="概率">
              <template slot="append">%</template>
            </el-input>
          </el-form-item>
          <el-form-item label="图片" prop="thumb">
            <el-upload
              :limit="100"
              :action="'/api/api/upload?ssid=' + ssid"
              :on-success="fileSuccess1"
              :show-file-list="false"
              :on-error="fileError"
              :on-change="onbefore"
            >
              <img v-if="item.img" :src="item.img" class="avatar" width="50" height="50" @click="active = item" />
              <el-button v-else size="small" type="infor" @click="active = item">点击上传</el-button>
            </el-upload>
          </el-form-item>
          <el-form-item :label="'类型' + (key + 1)">
            <el-select size="small" v-model="item.type" clearable placeholder="请选择">
              <el-option value="money" label="现金" />
              <el-option value="goods" label="商品" />
              <el-option value="credit" label="积分" />
            </el-select>
          </el-form-item>
          <el-form-item :label="'获取数量' + (key + 1)">
            <el-input v-model="item.num" placeholder="数量"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col>
          <el-form-item label="是否开启">
            <el-select size="small" v-model="form.is_open" clearable placeholder="请选择">
              <el-option value="Y" label="是" />
              <el-option value="N" label="否" />
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col>
          <el-form-item label="抽奖规则说明">
            <el-input
              type="textarea"
              :cols="20"
              :rows="10"
              v-model="form.remark"
              size="small"
              placeholder="请输入"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-col>
        <el-form-item>
          <el-button type="primary" native-type="submit">保存</el-button>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>
<script>
import Fetch from '../../utils/fetch'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      loading: false,
      form: {
        prize: [],
      },
      file: [],
      type: 'prize',
      active: 0,
    }
  },
  computed: {
    ssid() {
      return localStorage.getItem('ssid')
    },
  },
  mounted() {
    Fetch('/sys/config/info', { type: this.type }).then(r => {
      this.form = r.data
      if (this.form.image) {
        this.file = [
          {
            url: this.form.image,
            name: this.form.image,
          },
        ]
      }
    })
  },
  methods: {
    fileSuccess(res) {
      this.form.image = res.data.file
    },
    fileSuccess1(item) {
      console.log('123', 123)
      this.active.img = item.data.file
    },
    onSubmit() {
      this.loading = true
      Fetch('/sys/config/update', {
        type: this.type,
        ...this.form,
      })
        .then(() => {
          this.loading = false
          Message.success('操作成功')
        })
        .catch(() => (this.loading = false))
    },
    fileError() {
      this.$message.error('上传失败')
    },
    onbefore() {
      console.log('onbefore')
    },
  },
}
</script>
<style lang="less" scoped>
.form {
  /deep/ .el-input__inner {
    width: 90px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
